
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components:{
XHeader,InlineLoading,scroll
},
mounted(){
if(this.$route.params.JLBM){
this.JLBM = this.$route.params.JLBM;
};
this.init();
},
data(){
return {
JLBM: '',   // 挂号记录编码
resObj:{},
INTENT:true,
}
},
methods: {
goBack(){
if(this.$route.params.INTENT == '1'){
return;
}else{
this.$router.goBack()
}

},
goHome() {
this.$router.isBack = true;
this.$router.push('/home');
},
getJZZT(str){  // 获取就诊状态
switch (str) {
case 1:
return '待就诊';
break;
case 2:
return '已就诊';
break;
case 3:
return '已取消';
break;

default:
return str
break;
}
},
getPhone(CHZSJ){
return CHZSJ.substring(0,3)+'****'+CHZSJ.substring(7,11)

},
getTime(DJZSJ,YYSJ){
if(YYSJ=='1'){
YYSJ = '下午';
}else{
YYSJ = '上午';
}
var date = new Date(DJZSJ);
var Y = date.getFullYear() + '-'
var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-'
var D = date.getDate() + ' '

return DJZSJ?DJZSJ:''+ YYSJ;

},
getJOB(index){
if(index=='01'){
return "主任医师";
}
if(index=='02'){
return "副主任医师";
}
if(index=='03'){
return "主治医师";
}
if(index=='04'){
return "医师";
}
if(index=='05'){
return "医士";
}else{
return '';
}



},
init(){
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post('',{
"MSH.1": "myappointment",
"MSH.2": "getAppointment",
token:this.$root.loginInfo.token, //    token
cbm: this.JLBM    


}).then(res => {
this.$vux.loading.hide();
console.log(res)
this.resObj = res;
}).catch(error => {
console.log(error);
})
},
cancelYY(title){     // 取消预约
let THIS = this;
THIS.$vux.confirm.show({
title: title,
onConfirm () {
THIS.$vux.loading.show({
text: '取消中...'
})

THIS.$ajax.post('',{
"MSH.1": "myappointment",
"MSH.2": "cancelAppointment",
token:THIS.$root.loginInfo.token, //    token
cbm: THIS.JLBM,    // 挂号记录编码
}).then(res => {
console.log('取消：--------------')
THIS.$vux.loading.hide();
console.log('取消：'+res)
if(res == 1){
THIS.$vux.toast.show({
type:'type',
time: '1000',
text: '取消成功!',
onHide(){
THIS.init();
}
})

}

}).catch(error => {
console.log(error);
})
}
})

},
selectState(){
this.$ajax.post('',{
"MSH.1": "myappointment",
"MSH.2": "canelMaxNum",
'token':this.$root.loginInfo.token,
}).then(res => {
if(res.num==4){
this.cancelYY('取消预约后， 你将在一个月内不能使用平台在线预约服务，因您在一个月内已连续取消5次预约，为避免恶意刷号情况，将会短期内暂停您的使用权限。是否确认取消？');
}else if(res.num<4){
this.cancelYY('是否取消预约? ');
}else{
this.$vux.toast.show({
type:'type',
time: '1000',
text: '当月取消预约次数一上限!'
})

}

}).catch(error => {
console.log(error);
})



},
}
}



